import config from '../../configuration/config';

const SearchApi = () => {
  const getVideos = (searchTerm, pageToken, orderBy, searchType) => {
    const orderParam = orderBy || 'viewCount';
    const typeParam = searchType || 'video';
    let url = `${config.googleApiSearchUrl}?part=snippet&key=${config.googleApi}&q=${searchTerm}&type=${typeParam}&order=${orderParam}&maxResults=15`;
    if (pageToken) {
      url += `&pageToken=${pageToken}`;
    }

    const request = new Request(url);

    return fetch(request).then((response) => {
      if (response.status === 200) {
        return response.json();
      }

      return {};
    });
  };
  const getVideoDetails = (videoId) => {
    const request = new Request(`${config.googleApiVideoUrl}?part=statistics&key=${config.googleApi}&id=${videoId}`);
    return fetch(request).then((response) => {
      if (response.status === 200) {
        return response.json()
          .then(responseBody => responseBody.items
            .find(element => element.id === videoId).statistics);
      } return {};
    });
  };

  return { getVideos, getVideoDetails };
};
export default SearchApi;
