
function SwipeNavigation(container, onLeftSwipe, onRightSwipe) {
  const elementContainer = container;
  let xValue = null;
  let locked = false;

  const unify = e => (e.changedTouches ? e.changedTouches[0] : e);

  const lock = (e) => {
    xValue = unify(e).clientX;
    locked = true;
    container.classList.add('dragging');
  };

  function move(e) {
    if (locked) {
      const dx = unify(e).clientX - xValue;

      if (dx < -10) {
        onLeftSwipe();
      }
      if (dx > 10) {
        onRightSwipe();
      }

      locked = false;
      xValue = null;
      container.classList.remove('dragging');
    }
  }

  elementContainer.addEventListener('mousedown', lock, false);
  elementContainer.addEventListener('touchstart', lock, false);

  elementContainer.addEventListener('mouseup', move, false);
  elementContainer.addEventListener('touchend', move, false);
}

export default SwipeNavigation;
