import paginationTemplate from './pagination.html';
import './pagination.less';

function Button(index, onClick, isSelected) {
  const element = window.document.createElement('div');
  const tooltip = window.document.createElement('span');

  element.setAttribute('id', `button_${index + 1}`);
  element.classList.add(isSelected ? 'pagination-btns__current' : 'pagination-btns__other');
  element.classList.add('tooltip');
  element.textContent = index + 1;
  if (onClick) { element.addEventListener('click', () => onClick(index)); }

  tooltip.classList.add('tooltiptext');
  tooltip.textContent = index + 1;
  element.appendChild(tooltip);

  return element;
}

class Pagination {
  constructor(elementSelector, onSelectPage, onChunkEnd) {
    this.onSelectPage = onSelectPage;
    this.onChunkEnd = onChunkEnd;
    this.element = window.document.querySelector(elementSelector);
    this.element.innerHTML = paginationTemplate.trim();
  }

  init() {
    this.btnContainer.classList.add('active');
    this.btnContainer.innerHTML = '';
    this.page = 0;

    const pageCount = Math.ceil(this.resultCount / this.resultPerPage);

    for (let index = 0; index < pageCount; index += 1) {
      const btn = new Button(index, i => this.btnClick(i), index === this.currentPage);
      this.btnContainer.appendChild(btn);
    }
  }

  update() {
    const pageCount = Math.ceil(this.resultCount / this.resultPerPage);
    const currentButtonsQty = this.btnContainer.children.length;

    if (pageCount > currentButtonsQty) {
      for (let index = currentButtonsQty; index < pageCount; index += 1) {
        const btn = new Button(index, i => this.btnClick(i), index === this.currentPage);
        this.btnContainer.appendChild(btn);
      }
    } else {
      for (let index = 0; index < currentButtonsQty - pageCount; index += 1) {
        this.btnContainer.removeChild(this.btnContainer.lastChild);
      }
    }
  }

  get btnContainer() {
    return this.element.querySelector('.pagination-btns');
  }

  btnClick(index, force) {
    if (!force && (index === this.currentPage || index < 0)) {
      return;
    }

    this.onSelectPage(index, this.resultPerPage, this.currentPage);
    this.currentPage = index;
  }

  get currentPage() {
    return this.page;
  }

  set currentPage(page) {
    if (this.page === page) { return; }

    const previousBtn = this.page;
    this.page = page;
    const buttons = this.btnContainer.children;
    if (buttons.length > previousBtn) {
      buttons[previousBtn].classList.replace('pagination-btns__current', 'pagination-btns__other');
    }
    buttons[this.page].classList.replace('pagination-btns__other', 'pagination-btns__current');

    if (this.page >= buttons.length - 2) {
      this.onChunkEnd();
    }
  }

  next() {
    this.btnClick(this.currentPage + 1);
  }

  prev() {
    this.btnClick(this.currentPage - 1);
  }
}

export default Pagination;
