import elementTemplate from './result-card-component.html';
import './result-card-component.less';

const resultCardWidth = 300;
const animationLength = 500;

class ResultCard {
  constructor(containerElement, searchResult) {
    this.container = containerElement;
    this.element = window.document.createElement('div');
    this.element.innerHTML = elementTemplate;

    const {
      snippet: {
        title, description, thumbnails, channelTitle, publishedAt,
      }, statistics, id: { videoId },
    } = searchResult;
    this.elementId = `q${videoId}`;
    this.element.querySelector('.result-card').setAttribute('id', this.elementId);
    this.element.querySelector('.title h1').textContent = title;
    this.element.querySelector('.title').setAttribute('href', `https://www.youtube.com/watch?v=${videoId}`);
    this.element.querySelector('.info').textContent = description;
    this.element.querySelector('img').setAttribute('src', thumbnails.medium.url);
    this.element.querySelector('img').setAttribute('alt', title);
    this.element.querySelector('.statistics__male').textContent = channelTitle;
    this.element.querySelector('.statistics__calendar').textContent = publishedAt.slice(0, 10);
    this.element.querySelector('.statistics__eye').textContent = statistics.viewCount;
  }

  fadeIn(fadeClass) {
    setTimeout(() => {
      this.container.appendChild(this.element.firstChild);
      this.element = this.container.querySelector(`#${this.elementId}`);
      if (fadeClass) {
        this.element.classList.add(fadeClass);
        setTimeout(() => {
          this.element.classList.remove(fadeClass);
        }, animationLength);
      }
    }, animationLength);
  }

  fadeOut(fadeClass) {
    if (fadeClass) {
      this.element.classList.add(fadeClass);
    }
    setTimeout(() => { this.container.removeChild(this.element); }, animationLength);
  }
}

export { ResultCard, resultCardWidth };
export default ResultCard;
