import './search-component.less';
import elementTemplate from './search-component.html';
import SearchApi from '../services/search-api';
import { ResultCard, resultCardWidth } from '../card/result-card-component';
import Pagination from '../pagination/pagination';
import '../pictures/searcIcon.png';
import SwipeNavigation from '../services/swipe-navigation';

const searchResultSelector = '.search-result';
const searchApi = SearchApi();

class SearchComponent {
  constructor(elementSelector) {
    this.element = window.document.querySelector(elementSelector);
    this.resultCards = [];
  }

  get searchText() {
    return this.element.querySelector('.input-search').value;
  }

  get resultContainerWidth() {
    return this.element.querySelector(searchResultSelector).offsetWidth;
  }

  get searchResultContainer() {
    return this.element.querySelector('.search-result');
  }

  onSearchClick() {
    this.element.querySelector('.search-result').innerHTML = '';

    searchApi.getVideos(this.searchText).then(({ nextPageToken, prevPageToken, items }) => {
      this.items = items;
      this.nextPage = nextPageToken;
      this.previousPage = prevPageToken;
      this.pagination.resultCount = items.length;
      this.pagination.resultPerPage = Math.floor(this.resultContainerWidth / resultCardWidth);
      this.pagination.init();
      this.renderResult(this.pagination.currentPage, this.pagination.resultPerPage, -1);
    });
  }

  renderResult(page, itemsPerPage, prevPage) {
    const start = page * itemsPerPage;
    const end = start + itemsPerPage;
    const forward = page > prevPage;

    const promises = this.items.slice(start, end)
      .map(({ id: { videoId } }, index) => searchApi.getVideoDetails(videoId).then((statistics) => {
        this.items[index + start] = { ...this.items[index + start], statistics };
      }));

    Promise.all(promises).then(() => {
      this.resultCards.forEach((e) => { e.fadeOut(forward ? 'slideOutRight' : 'slideOutLeft'); });
      this.resultCards = [];
      this.items.slice(start, end).forEach((item) => {
        const resultCard = new ResultCard(this.searchResultContainer, item);
        this.resultCards.push(resultCard);
        resultCard.fadeIn(forward ? 'slideInLeft' : 'slideInRight');
      });
    });
  }

  getNewChunk() {
    searchApi.getVideos(this.searchText, this.nextPage)
      .then(({ nextPageToken, prevPageToken, items }) => {
        this.items.push(...items);
        this.nextPage = nextPageToken;
        this.previousPage = prevPageToken;
        this.pagination.resultCount = this.items.length;
        this.pagination.resultPerPage = Math.floor(this.resultContainerWidth / resultCardWidth);
        this.pagination.update();
      });
  }

  render() {
    this.element.innerHTML = elementTemplate.trim();
    this.element.querySelector('.btn-search').addEventListener('click', () => { this.onSearchClick(); });
    SwipeNavigation(this.searchResultContainer,
      () => this.pagination.next(), () => this.pagination.prev());

    this.pagination = new Pagination('.pagination',
      (page, itemsPerPage, prevPage) => this.renderResult(page, itemsPerPage, prevPage),
      () => this.getNewChunk());

    window.onresize = () => {
      const { pagination: { resultPerPage, currentPage }, items, resultContainerWidth } = this;
      const newResultPerPage = Math.floor(resultContainerWidth / resultCardWidth);
      if (!items || items.length === 0 || resultPerPage === newResultPerPage) {
        return;
      }
      const currentResult = resultPerPage * currentPage;
      const newPage = Math.floor(currentResult / newResultPerPage);

      this.pagination.resultPerPage = Math.floor(resultContainerWidth / resultCardWidth);
      this.pagination.update();
      this.pagination.btnClick(newPage, true);
    };
  }
}

export default SearchComponent;
